import React, {useRef, useEffect} from "react";
import styles from '../styles/Text.module.css';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';


function Text() {

    let textRef = useRef(null);
    let titleRef = useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        gsap.from(textRef.current, {
            duration: 1,
            x: 50,
            opacity: 0,
            scrollTrigger: {
                trriger: textRef.current,
                toggleActions: 'restart resume restart resume',
                start: '76% center',
                end: 'bottom center'
            },
        });
    }, [textRef]);

    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        gsap.from(titleRef.current, {
            duration: 2,
            fontSize: 10,
            y: 50,
            opacity: 0,
            scrollTrigger: {
                trriger: titleRef.current,
                start: '75% center',
                end: 'bottom center',
                toggleActions: 'restart resume resume resume'
            },
        });
    }, [textRef]);

    return (

        <section className={styles.container}>
            <h2 ref={titleRef} className={styles.title}>Some Text</h2>
            <div ref={textRef} className={styles.text}>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid
                culpa doloremque illum ipsa itaque nisi quod vel voluptates. Libero molestias, officia? Blanditiis
                dignissimos nesciunt odit pariatur quod saepe, tempora tempore! Aperiam dicta distinctio ex laborum odit
                perspiciatis praesentium quas reprehenderit tempora vero. Accusantium, amet animi asperiores atque
                commodi deleniti deserunt ducimus earum eum hic laudantium libero molestiae nesciunt nobis porro quae
                quod quos repellat reprehenderit totam, veniam voluptates voluptatibus? Aliquid aspernatur atque
                blanditiis consequuntur cumque deserunt, distinctio dolorem doloribus eligendi enim eos esse eveniet
                excepturi harum illum laudantium libero maiores maxime minus nam neque nobis non odit optio pariatur
                possimus praesentium quae quaerat quibusdam quisquam quo quos repellat saepe sequi similique tempora
                voluptas. Consequatur deserunt harum iste minima modi molestiae nesciunt, odio officiis, optio possimus
                quaerat qui ratione recusandae soluta temporibus vel velit vero. Error nihil nulla numquam sed tenetur.
                A, asperiores debitis dolore dolorem inventore ipsa laboriosam modi nam nobis, obcaecati pariatur quam
                ratione, recusandae repellendus saepe sequi soluta. Amet animi architecto deleniti eveniet perspiciatis
                quae saepe totam vel! Amet animi assumenda atque, dolore ea eos esse eum, eveniet, fuga impedit
                inventore ipsa magnam modi nam officiis omnis pariatur perspiciatis quae repellat sed similique
                temporibus ullam veritatis voluptate voluptatem.</div>
        </section>
    )
}

export default Text;