import React, {createRef, useEffect} from "react";
import gsap, {TimelineMax} from 'gsap';
import {ScrollTrigger} from "gsap/ScrollTrigger";
import styles from '../styles/Circles.module.css';


function Circle({i, containerRef, appRef}) {
    const circle = createRef();

    useEffect(() => {
        if (circle.current) {
            let duration = 1;
            let animate = {...gsap};
            animate.registerPlugin(ScrollTrigger);
            let odj = {
                top: 300,
                left: 300,
                duration: duration * 3,
                delay: duration * (i + 1) + 0.3,
                ease: 'elastic',
            };

            let delay = 0.2 * (7 - i);

            animate = animate.timeline({
                scrollTrigger: {
                    trriger: containerRef.current,
                    pin: containerRef.current,
                    start: 'top 180px',
                    end: () => '+=' + appRef.current.offsetHeight * 3 / 4,
                    scrub: true,
                    toggleActions: 'restart pause resume pause'
                }
            });

            switch (i) {
                case 0:
                    animate.to(circle.current,  {top: 300, left: 550,duration,delay})
                        .to(circle.current, odj);
                    break;
                case 1:
                    animate.to(circle.current, {top: 300, left: 50, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 2:
                    animate.to(circle.current, {top: 50, left: 300, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 3:
                    animate.to(circle.current, {top: 550, left: 300, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 4:
                    animate.to(circle.current, {top: 128, left: 472, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 5:
                    animate.to(circle.current, {top: 128, left: 128, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 6:
                    animate.to(circle.current, {top: 472, left: 472, duration,delay})
                        .to(circle.current, odj);
                    break;
                case 7:
                    animate.to(circle.current, {top: 472, left: 128, duration,delay})
                        .to(circle.current, odj);
                    break;
            }
        }
    }, [circle]);

    return (<div ref={circle} className={styles.circle} i={i}/>)
}

export default Circle;